import React, { useState } from 'react';
import Form from '@rjsf/core';
import validator from '@rjsf/validator-ajv8';
import axios from 'axios';
import './HandParamsForm.css'; // Import file CSS ty chnh

const handParamsSchema = {
    title: 'NHP THNG S TAY V CNG TAY',
    type: 'object',
    required: ['taytrai', 'tayphai', 'cangtaytrai', 'cangtayphai'],
    properties: {
        taytrai: { type: 'number', title: 'TAY TRI' },
        tayphai: { type: 'number', title: 'TAY PHI' },
        cangtaytrai: { type: 'number', title: 'CNG TAY TRI' },
        cangtayphai: { type: 'number', title: 'CNG TAY PHI' },
    },
};

const HandParamsForm = () => {
    const [formData, setFormData] = useState({});

    const handleSubmit = async ({ formData }) => {
        try {
            console.log('D liu gi i:', formData);

            const response = await axios.post(
                'https://ap-southeast-1.aws.data.mongodb-api.com/app/data-byptt/endpoint/UPMOTIONBACKEND',
                formData
            );

            console.log('Kt qu t server:', response.data);
            alert('D LIU  C LU LI. CM N BN  S DNG');
            // Reset form sau khi submit thnh cng
            setFormData({});
        } catch (error) {
            console.error('Li khi gi d liu:', error);
        }
    };

    return (
        <div className="hand-params-form-container">
            <Form
                schema={handParamsSchema}
                validator={validator}
                formData={formData}
                onChange={({ formData }) => setFormData(formData)}
                onSubmit={handleSubmit}
            />
        </div>
    );
};

export default HandParamsForm;
